import React, { useState, useEffect } from "react";

import { Link } from 'react-router-dom';



const ProductDisplay = () => (
    <section>
      <div className="product">

        <div className="description">

        <h5 className="donateAmount">$20.00</h5>
        </div>
      </div>





      <div className="row justify-content-center">
      <form className="cardForm" method="POST">

        <Link to="/create-checkout-session" className="cardLink">
            <div className='cardContainer row justify-content-center'>
            {/* <Tilt> */}
                <div className='card your-element' data-tilt>
                    <h3 className='bank'>Recipyy Bank</h3>
                    <img src='./chip.png' className='chip' />
                    <img src='./contactless.png' className='contactless' />
                    <h3 className='number'>4242 4242 4242 4242</h3>
                    <h5 className='card-holder'><span>card holder</span><br />
                        GW Bootcamp
                    </h5>
                    <h5 className='valid'><span>Valid Thru</span><br />04/24</h5>
                    <img src='./visa.png' className='visa' />
                </div>
            {/* </Tilt> */}
            </div>
        </Link>



      </form>
      </div>





    </section>
  );
  
  const Message = ({ message }) => (
    <section>
      <p>{message}</p>
    </section>
  );






const Donation = () => {




    const [message, setMessage] = useState("");

    useEffect(() => {
      const query = new URLSearchParams(window.location.search);
      if (query.get("success")) {
        setMessage("Thank you for your donation.");
      }
      if (query.get("canceled")) {
        setMessage(
          "Donation Canceled."
        );
      }
    }, []);




    return (




                <div>
                    {message ? (
                        <Message message={message} />
                    ) : (
                        <ProductDisplay />
                    )}
                </div>





    )
}

export default Donation;